@import url("https://fonts.googleapis.com/css2?family=Courgette&family=Qwitcher+Grypen&family=Shadows+Into+Light&display=swap");
@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@500&family=Manrope&family=Montserrat:wght@300&display=swap");

.design {
  display: flex;
  align-content: center;
  align-items: center;
  font-family: "Manrope", sans-serif;
  background-color: white;
  color: black;
}

.information {
  width: 40%;
  letter-spacing: 1.5px;
  text-align: justify;
  padding: 0% 5%;
}
.pic {
  height: 100vh;
  width: 50%;
  overflow: hidden;
}
.design-pic {
  width: 350%;

  position: relative;
  animation-name: example;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in;
  animation-direction: alternate;
}

@keyframes example {
  0% {
    left: 0vh;
    top: 0vh;
  }

  100% {
    left: -1900px;
    top: -800px;
  }
}

.sema {
  text-align: end;

  font-size: 15px;
  font-family: "Courgette", cursive;
}

.next {
  height: 5vh;
  background-color: #ff7b00ad;
  border: none;
  color: white;
  border-radius: 10px;
  padding: 3%;
  margin-top: 10%;
  display: flex;
  align-items: center;
  font-family: "Monserrat", sans-serif;
}

.next:hover {
  cursor: pointer;
}
